.tabs {
    max-width: 600px;
  }
  
  .controls {
    display: flex;
    margin-bottom: 4px;
  }
  
  .controls__item {
    padding: 10px 20px;
    border: 1px solid gray;
    color: black;
    font-weight: 500;
    text-decoration: none;
  }
  
  .controls__item--active {
    background-color: palevioletred;
    color: white;
  }
  
  .controls__item:not(:last-child) {
    margin-right: 4px;
  }
  
  .pane {
    display: none;
    padding: 15px;
  
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
    animation: fadeEffect 500ms;
  }
  
  .pane--active {
    display: block;
  }
  
  .pane__fruit {
    margin-top: 0;
    margin-bottom: 16px;
    font-size: 48px;
  }
  
  .pane__text {
    margin-bottom: 0;
  }